import { PAGE_SIZE } from '@/assets/config'
import { CODE, StudentWorksKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'studentWorksTable',
  data () {
    return {
      code: CODE.StudentWorks,
      dataKeys: StudentWorksKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {
        page_num: PAGE_SIZE
      },
      searchOptions: [
        { label: '发表日期', key: 'time', type: 'date' },
        { label: '第一作者', key: 'first_author' },
        { label: '学号', key: 'study_num' },
        { label: '专业', key: 'specialty' },
        { label: '导师', key: 'teacher' },
        { label: '第一通讯作者', key: 'first_communication_author' },
        { label: '论文名称', key: 'name' },
        { label: '刊物名称', key: 'publication_name' },
        { label: '发表时分区及影响因子', key: 'publishing_partition_influences' },
        { label: '五年影响因子', key: 'five_year_influences' }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables(this.page)
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      this.pageSize = this.condition.page_num
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.StudentWorks.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.StudentWorks.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/studentWorks/new', '学生发表著作情况-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/studentWorks/' + item.id, '学生发表著作情况-详情-' + item.id)
    }
  },
  created () {
    this.operation = Cache.pageOperation('talent_development')
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
    })
    if (this.$route.query['key']) {
      this.condition['key'] = this.$route.query['key'];this.condition['status'] = 3
    }
    this.getDataTables()
  },
  mounted () {
  }
}



